<template>
  <div ref="echartsDiv" style="width: auto; height:300px;padding:0px;"></div>
</template>

<script>

export default {
  name: "Echarts",
  props: {
    echartsData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      myEcharts: null,
    };
  },
  methods: {
    drawCharts() {
      this.myEcharts = this.$echarts.init(this.$refs.echartsDiv);
      // console.log(this.echartsData);
      this.myEcharts.setOption(this.echartsData);
    },
  },
  mounted() {
    // console.log(this.$refs.echartsDiv);
    
    this.drawCharts();

    window.addEventListener("resize", () => {
      // 这里用箭头函数
      this.myEcharts.resize();
      
    });
  },
};
</script>

