<template>
  <div>
    <!-- 第一列 -->
    <el-row :gutter="15" >
      <el-col :xs="24" :sm="12" :md="12" :lg="9" class="col_margin_top">
        <el-card class="card_col_height">
          <el-row>
            <el-col :xs="24" :sm="12" :md="12" :lg="12">
              <img :src="userImg" alt="头像" class="index_img" />
            </el-col>

            <el-col :xs="24" :sm="12" :md="12" :lg="12" class="index_name">
              <h2>{{indexData.admin_name}}</h2>
              <span>管理员</span>
              <span>权限</span>
            </el-col>
          </el-row>

          <el-row>
            <!-- <hr class="hrcolor" /> -->
            <p><br /><br /></p>
            <p class="index_xinxi">
              <span>上次登录时间：</span><span> {{timeFormat}}</span>
            </p>
            <p class="index_xinxi">
              <span>上次IP地址：</span><span> {{NumberIP}}</span>
            </p>
          </el-row>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="5" class="col_margin_top">
        <el-card class="card_col_height">
          <div id="card_1">
            <Echarts :echartsData="option1_diQue" :style="{ height: 250 + 'px', width: 'auto'}"></Echarts>
          </div>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="5" class="col_margin_top">
        <el-card class="card_col_height">
          <div id="card_2">
            <Echarts :echartsData="option_laiYuan" :style="{ height: 250 + 'px', width: 'auto'}" ></Echarts>
          </div>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12" :lg="5" class="col_margin_top">
        <el-card class="card_col_height">
          <div id="card_3">
            <Echarts :echartsData="option_fuZai" :style="{ height: 250 + 'px', width: 'auto'}"></Echarts>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- 第二列 -->
    <el-row style="margin-top: 15px; height: 300px" :gutter="15">
      <el-col :span="9">
        <el-card>
          <template>
            <!-- 表格 -->
            <el-table class="tableleft" :data="tableData" style="width: 100%">
              <el-table-column prop="date" label="时间" width="130">
              </el-table-column>
              <el-table-column prop="name" label="ip地址" width="130">
              </el-table-column>
              <el-table-column prop="address" label="地址"></el-table-column>
            </el-table>
          </template>
        </el-card>
      </el-col>
      <!-- 右侧折线图 -->
      <el-col :span="15">
        <el-card>
          <div id="card_4" >
            <Echarts :echartsData="option_fangWen" :style="{ height: 300 + 'px', width: 'auto'}" ></Echarts>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import service from "@/utils/request";
import Echarts from "../../components/Echarts";
export default {
  name: "Home",
  components: { Echarts },
  data() {
    return {
      indexData:'',
      userImg: require("../../assets/images/user.jpg"),
     
      tableData: [
        {
          date: "2021-11-14",
          name: "10.101.3.150",
          address: "浙江省杭州市",
        },
        {
          date: "2021-11-12",
          name: "10.101.3.152",
          address: "浙江省杭州市",
        },
        {
          date: "2021-11-12",
          name: "10.101.3.150",
          address: "浙江省杭州市",
        },
        {
          date: "2021-11-12",
          name: "10.101.3.151",
          address: "浙江省杭州市",
        },
        {
          date: "2021-11-12",
          name: "10.101.3.151",
          address: "浙江省杭州市",
        },
      ],
      option_fangWen: {
        //近期访问量
        title: {
          text: " 近期访问量",
          left: "left",
          fontSize: 14,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999",
            },
          },
        },
        xAxis: {
          type: "category",
          data: [
            "1月",
            "2月",
            "3月",
            "4月",
            "5月",
            "6月",
            "7月",
            "8月",
            "9月",
            "10月",
            "11月",
            "12月",
          ],
          axisPointer: {
            type: "shadow",
          },
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [
              {
                value: 1220,
                itemStyle: {
                  color: "rgb(103,224,227)",
                },
              },
              {
                value: 2020,
                itemStyle: {
                  color: "rgb(55,162,218)",
                },
              },
              {
                value: 1003,
                itemStyle: {
                  color: "rgb(115,192,222)",
                },
              },
              {
                value: 1520,
                itemStyle: {
                  color: "rgb(130,99,99)",
                },
              },
              {
                value: 720,
                itemStyle: {
                  color: "rgb(230,99,99)",
                },
              },
              {
                value: 1530,
                itemStyle: {
                  color: "rgb(250,200,88)",
                },
              },
              {
                value: 1020,
                itemStyle: {
                  color: "rgb(73,199,255)",
                },
              },
              {
                value: 1232,
                itemStyle: {
                  color: "rgb(145,204,117)",
                },
              },
              {
                value: 1590,
                itemStyle: {
                  color: "rgb(164,252,200)",
                },
              },
              {
                value: 2290,
                itemStyle: {
                  color: "rgb(248,96,182)",
                },
              },
              {
                value: 2190,
                itemStyle: {
                  color: "rgb(22,202,212)",
                },
              },
              {
                value: 0,
                itemStyle: {
                  color: "rgb(145,204,117)",
                },
              },
            ],
            type: "bar",
          },
        ],
      },
      option1_diQue: {
        //饼图
        title: {
          text: "用户访问地区",
          fontSize: 14,
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          top: "10%",
          left: "left",
        },
        series: [
          {
            top: "28%",
            name: "数据",
            type: "pie",
            radius: ["40%", "85%"],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 2,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "14",
                fontWeight: 500,
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: 1048, name: "杭州" },
              { value: 735, name: "上海" },
              { value: 580, name: "北京" },
              { value: 484, name: "广州" },
              { value: 300, name: "深圳" },
              { value: 600, name: "其他" },
            ],
          },
        ],
      },
      option_fuZai: {
        //饼图
        title: {
          text: "当前负载",
          fontSize: 14,
        },

        series: [
          {
            type: "gauge",
            radius: "88%", //仪表盘半径
            axisLine: {
              lineStyle: {
                width: 20,
                color: [
                  [0.3, "#67e0e3"],
                  [0.7, "#37a2da"],
                  [1, "#fd666d"],
                ],
              },
            },
            pointer: {
              //指针
              itemStyle: {
                color: "inherit",
              },
              length: "60%",
              width: 7,
            },
            axisTick: {
              distance: -30,
              length: 15,
              lineStyle: {
                color: "#fff",
                width: 2,
              },
            },
            splitLine: {
              distance: -28,
              length: 15,
              lineStyle: {
                color: "#fff",
                width: 2,
              },
            },
            axisLabel: {
              color: "inherit",
              distance: 35,
              fontSize: 12,
            },
            detail: {
              valueAnimation: true,
              formatter: "{value}%",
              color: "inherit",
              fontSize: 24,
            },
            data: [
              {
                value: 65,
              },
            ],
            center: ["50%", "65%"],
          },
        ],
      },
      option_laiYuan: {
        title: {
          text: "用户访问来源",
          fontSize: 14,
        },
        legend: {
          data: ["PC", "M-Web"],
          top: "12%",
          left: "left",
        },
        radar: {
          shape: "circle",
          indicator: [
            { text: "Chrome", max: 10000 },
            { text: "IE9+", max: 10000 },
            { text: "Safari", max: 10000 },
            { text: "Firefox", max: 10000 },
            { text: "IE8-", max: 10000 },
          ],
          center: ["50%", "62%"], //位置
          padding: [-10, -13], // 控制文字padding
        },
        series: [
          {
            name: "PC vs M-Web",
            type: "radar",
            data: [
              {
                value: [10000, 3000, 3000, 5000, 5000, 9000],
                name: "PC",
              },
              {
                value: [8000, 5000, 5000, 2000, 4000, 8000],
                name: "M-Web",
              },
            ],
          },
        ],
      },
     
    };
  },

  methods: {
    dateFormat(value, gs) {
      let date = new Date(parseInt(value) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      let second = date.getSeconds();
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;
      let yms = y + "年" + m + "月" + d + "日 ";
      let hms = h + ":" + minute + ":" + second;

      if (gs) {
        return yms + hms;
      } else {
        return yms;
      }
    },  //时间格式化
  },
  computed:{
     NumberIP: function () {
      let num =this.indexData.admin_last_login_ip;
      let str;
      let tt = new Array();
      tt[0] = (num >>> 24) >>> 0;
      tt[1] = ((num << 8) >>> 24) >>> 0;
      tt[2] = (num << 16) >>> 24;
      tt[3] = (num << 24) >>> 24;
      str = String(tt[0]) + "." + String(tt[1]) + "." + String(tt[2]) + "." + String(tt[3]);
      return str;
    },
    timeFormat() {
      return this.dateFormat(this.indexData.admin_last_login_time,1);
    },
  },
  mounted() {
    service
      .post("/admin/index")
      .then((result) => {
        this.indexData=result.data.data;
        // console.log(this.indexData);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  watch: {},
};
</script>

<style scoped>
.col_margin_top {
  margin-top: 10px;
}
#echartdom {
  height: 300px;
  min-height: 280px;
  width: auto;
}
#card_Doughnut,
#card_stage,
#card_3 {
  padding: 0;
  height: 260px;
  width: auto;
}

.card_col_height {
  height: 290px;
}
.tableleft {
  height: 300px;
  min-height: 280px;
}
.button {
  padding: 0;
  float: left;
}
.index_img {
  height: 150px;
  width: auto;
  border-radius: 50%;
}
.index_name {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  text-align: left;
}
.index_name span {
  text-align: left;
  font-size: 14px;
}
.hrcolor {
  color: grey;
  margin-bottom: 20px;
  margin-top: 10px;
}
.index_xinxi {
  margin-top: 5px;
  justify-content: start;
  text-align: left;
}
</style>
